.content-space {
  width: auto;
  min-width: 960px;
  margin: 0 auto;
}
.slider-wrapper {
  position: relative;
  width: 1020px;
  margin: 64px auto 11px auto;
  height: 620px;
}
.hexagon-rows {
  width: 100%;
  height: auto;
}
.hexagon-row {
  margin: 0 auto;
  width: 821px;
  height: 207px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
