  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
}
.hexagon-row.hexagon-row-middle {
  width: 1020px;
  margin-top: -39px;
  margin-bottom: -39px;
}
.hexagon-border-l,
