.hexagon-border-l {
  float: left;
  margin: 51px 8px 0px 0px;
}
.hexagon-border-r {
  float: right;
  margin: 51px 0px 0px 8px;
}
.hexagon-wrapper {
  display: inline-block;
  opacity: 0;
  width: 179px;
  height: 207px;
  margin: 0px 11px 0px 10px;
  cursor: pointer;
}
.hexagon {
  position: relative;
  margin: 1px 0 0 0;
  width: 180px;
  height: 206px;
  background-size: 180px 206px;
  background-position: center center;
  background-repeat: no-repeat;
  z-index: 1;
}
.slider-controls {
  float: left;
  height: auto;
  width: 960px;
  margin: 59px 0px 0px 30px;
