}
.slider-controls > ul {
  padding: 0;
  margin: 0;
}
.slider-control {
  float: left;
  margin: 0px 3px 0px 0px;
  width: 6px;
  height: 6px;
  background: #ddd2c5;
  list-style: none;
  cursor: pointer;
}
.slider-control.slider-control-active {
  background: #8b8784;
}
