.layout-main {
  max-width: 1020px;
  width: auto;
  min-width: 900px;
  min-height: 100px;
  height: auto;
  margin: 0 auto;
  padding: 13px 0px 12px 0px;
}
.fltr-cat {
  margin: 0px 0px 20px 0px;
}
.fltr-cat-caption {
  display: block;
  font-weight: bold;
  font-size: 15px;
  margin: 0px 0px 5px 0px;
}
.fltr-checkbox {
  cursor: pointer;
}
.fltr-label {
  margin: 0px 15px 0px 0px;
  padding: 0px 0px 0px 6px;
  font-size: 15px;
  cursor: pointer;
}
.modal-header > h2 {
  margin: 3px 0px;
}
.createNewPostButton {
  background: #4896d9;
  border-color: #428bca;
}
.createNewPostButton:hover,
.createNewPostButton:active,
.createNewPostButton:focus {
  background: #428bca;
  border-color: #3c7db5;
  outline: none!important;
}
.updatePostButtonStyle {
  background: none;
  border: none;
  padding: 0;
  color: #428bca;
}
.updatePostButtonStyle:hover {
  color: #2a6496;
}
.deletePostButton:hover {
  color: #2a6496;
  text-decoration: none;
}
.deletePostButton:active,
.deletePostButton:visited,
.deletePostButton:focus {
  text-decoration: none;
}
.fltr-check {
  background: url(../img/checkboxes.png) no-repeat;
  padding: 0px 0px 0px 18px;
  display: inline-block;
  float: left;
  height: 12px;
  font-size: 14px;
  line-height: 14px;
  margin: 0px 20px 0px 0px;
  cursor: pointer;
  color: #000;
}
.fltr-check:hover,
.fltr-check:active,
.fltr-check:visited,
.fltr-check:focus {
  text-decoration: none;
  color: #000;
}
.fltr-check.active {
  background-position: 0 -13px;
}
